import React, { Component } from 'react'
import Card from '../Shared/CardUI'
import img1 from '../assets/img1.jpg'
import img2 from '../assets/img2.jpg'
import img3 from '../assets/img3.jpg'

class Cards extends Component {
  render () {
    return (
      <div className='container-fluid d-flex justify-content-center'>
        <div className='row'>
          <div className='col-md-4'>
            <Card
              imageSource={img1}
              altText='Solutions'
              cardTitle='Solutions'
              cardText='Image With many enterprises shifting towards the digital
                        transformation, Robotic Process Automation (RPA) adds to the benefits
                        for the industries as a whole. RPA needs suitable design, planning,
                        and governance to strengthen businesses. It mimics people
                        interactions, high-performance tasks, automate operational processes,
                        minimizes errors, and improves efficiency and performance, thus
                        significantly reducing costs. With this technology, enterprises can
                        transform their business processes digitally, and the workforce can
                        effectively focus on other high-value tasks. RPA robots utilize the
                        user interface for data capturing and critically analyze the tasks on
                        a regular basis. It efficiently interprets the actions and
                        communicates in order to perform and save up on various repetitive
                        tasks.'
            />
          </div>
          <div className='col-md-4'>
            <Card
              imageSource={img2}
              altText='Services'
              cardTitle='Services'
              cardText='Image With many enterprises shifting towards the digital
                    transformation, Robotic Process Automation (RPA) adds to the benefits
                    for the industries as a whole. RPA needs suitable design, planning,
                    and governance to strengthen businesses. It mimics people
                    interactions, high-performance tasks, automate operational processes,
                    minimizes errors, and improves efficiency and performance, thus
                    significantly reducing costs. With this technology, enterprises can
                    transform their business processes digitally, and the workforce can
                    effectively focus on other high-value tasks. RPA robots utilize the
                    user interface for data capturing and critically analyze the tasks on
                    a regular basis. It efficiently interprets the actions and
                    communicates in order to perform and save up on various repetitive
                    tasks.'
            />
          </div>
          <div className='col-md-4'>
            <Card
              imageSource={img3}
              altText='RPA'
              cardTitle='RPA'
              cardText='Image With many enterprises shifting towards the digital
                        transformation, Robotic Process Automation (RPA) adds to the benefits
                        for the industries as a whole. RPA needs suitable design, planning,
                        and governance to strengthen businesses. It mimics people
                        interactions, high-performance tasks, automate operational processes,
                        minimizes errors, and improves efficiency and performance, thus
                        significantly reducing costs. With this technology, enterprises can
                        transform their business processes digitally, and the workforce can
                        effectively focus on other high-value tasks. RPA robots utilize the
                        user interface for data capturing and critically analyze the tasks on
                        a regular basis. It efficiently interprets the actions and
                        communicates in order to perform and save up on various repetitive
                        tasks.'
            />
          </div>
        </div>
      </div>
    )
  }
}

export default Cards
