import React, { Component } from 'react'
import Card from '../Shared/CardUI'
import '../Shared/Footer.css'

class Footer extends Component {
  render () {
    return (
      <React.Fragment>
        <div className='container-fluid d-flex justify-content-center'>
          <div className='row'>
            <div className='col-md-4'>
              <Card
                imageSource={''}
                altText=''
                cardTitle='Contact'
                cardText='Block: 456, Road: 1234,Kingdom of Bahrain'
              />
            </div>
            <div className='col-md-4'>
              <Card
                imageSource={''}
                altText=''
                cardTitle='Services'
                cardText='Professional Services'
              />
            </div>
            <div className='col-md-4'>
              <Card
                imageSource={''}
                altText=''
                cardTitle='FORM'
                cardText='Email Form'
              />
            </div>
          </div>
        </div>

        <p className='text-center small copyright'>© SaBTech Consultancy WLL</p>
      </React.Fragment>
    )
  }
}

export default Footer
